	export default {
		data() {
			return {
				statusBarHeight: 0,
				isSending: false,
				intervalObj: {}, 
				time: 60,
				count_text: '发送验证码',
				account: '',
                password: '',
			};
		},
		methods:{
			closeLogin: function(){
				uni.navigateBack({
					delta: 1
				});
			},
			goToLogin(){
				uni.navigateTo({
					url: '/pages/login/login'
				});
			},
			getCodeAction(){
				if(!this.isSending){
					this.getCode();
				}
			},
			async loginAction(){
				
			},
			async getCode(){
				console.log(this.account);
				if((!this.account) || (this.account.length != 11)){
					uni.showToast({
					  icon: 'none',
					  title: '手机号输入错误',
					  duration: 1000
					});
					return;
				}
				this.isSending = true;
					this.countDown();
					return;
				
				let res = await sendCode({phone: this.account});
				if(res.code == 1000){
					uni.showToast({
					  icon: 'none',
					  title: '发送成功',
					  duration: 1000
					});
					this.isSending = true;
					this.countDown();
				}else{
					uni.showToast({
					  icon: 'none',
					  title: '发送失败',
					  duration: 1000
					});
				}
			},
			countDown() {
				let that = this;
			    this.intervalObj = setInterval(function() {
			        that.time--;
			        if (that.time <= 0) {
			            that.time = 60;
			            clearInterval(that.intervalObj);
						that.count_text = "重新获取";
			        } else {
						that.count_text = "重新获取("+that.time+"s)";
			        }
			
			    }, 1000);
			},
		},
		onLoad() {
			this.statusBarHeight = uni.getSystemInfoSync().statusBarHeight + 'px';
		}
	}
